.free {
  background-color: $color-sapphire-blue;
  padding: 62px 170px 60px 30px;

  @include vp-1199 {
    padding-right: 30px;
    padding-top: 70px;
    padding-bottom: 69px;
  }

  @include vp-767 {
    padding: 69px 0;
  }


  h2 {
    font-family: $ff-bebas-neue;
    font-size: 49px;
    line-height: 64px;
    letter-spacing: 0.03em;
    text-transform: lowercase;
    color: $color-default-white;
    margin: 0;

    @include vp-1199 {
      font-size: 42px;
      line-height: 100%;
    }

    @include vp-767 {
      font-size: 32px;
    }
  }
}

.free__form {
  width: 410px;
  margin-right: 0;
  margin-left: auto;

  @include vp-1199 {
    width: 472px;
    margin: 0 auto;
    position: relative;
    left: -15px;
  }

  @include vp-767 {
    width: auto;
    margin: 0 $mobile-margin-asade;
    position: static;
  }

  input {
    border: none;
    border-bottom: 1px solid $color-light-slate-grey;
    width: 100%;
    font-family: $ff-roboto;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: $color-default-white;
    background-color: transparent;
    padding: 0;
    padding-bottom: 5px;

    &::placeholder {
      font-family: $ff-roboto;
      font-weight: 400;
      font-size: 14px;
      line-height: 16px;
      color: $color-default-white;
    }

    &:hover {
      opacity: 0.7;
    }
  }
}

.free__form-list {
  display: grid;
  grid-template-columns: 190px 190px;
  gap: 30px;
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 31px;

  @include vp-1199 {
    grid-template-columns: 206px 206px;
    gap: 60px;
    margin-top: 33px;
  }

  @include vp-767 {
    grid-template-columns: none;
    grid-template-rows: auto auto;
    gap: 34px;
    margin-top: 36px;
  }
}

.free__button {
  display: block;
  width: 100%;
  margin-top: 29px;
  padding: 16px 30px;
  border: 3px solid $color-default-white;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: transparent;
  color: $color-default-white;
  cursor: pointer;

  @include vp-767 {
    margin-top: 38px;
  }

  &:hover {
    opacity: 0.5;
  }
}
